.navigation.pxy__30{
   
    display: flex;
    width: 100%;
    background-color: #0d0b0b;
    position: fixed;
    margin-top: -107;
    

}

.logo{
    margin-top: -2%;
}

.home__bg{
    position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  display: flex;
  flex-direction: column;
  
 
  color: white;
}
li.nav__items{
    font-size: 18px;
}
.home__content{
    padding-top: 245px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
   
}

h2.home__text{
    font-size: 50px;
    line-height: 54px;
    font-weight: 800;
    font-family: Montserrat,sans-serif;
    text-align: center;
}
h3.home__text{
    font-size: 30px;
    font-weight: 800;
    font-family: Montserrat,sans-serif;
    line-height: 40px;
    text-align: center;
    color: #F9004D;;
}

.header{
    transition: .4s all ease;
    width: 100%;
}
.header.active{
    position: fixed;
    z-index: 1000;
    width: 100%;
    
    background-color: #191919;
}
.toggle__menu {
    display: flex;
    justify-content: flex-end;
    float: right;
    z-index: 1000;
    width: 100%;
}
ul.sidebar.d__flex {
    flex-wrap: wrap;
    height: 100%;
    position: fixed;
    right: 0%;
    background: #010101;
    justify-content: center;
    width: 300px;
    top: 0%;
    flex-direction: column;
}
li.sideNavbar{
    padding: 25px 30px;
}
/* RESPONSIVE */
@media only screen and (max-width: 768px){
 .header{
     justify-content: space-between;
 }
 .navigation.pxy__30 {
         display: none; 
        
    }
h2.white.pz__10 {
    font-size: 50px!important;
}
svg.bi.bi-justify.white.pointer {
    width: 30px;
    height: 30px;
}
}
@media only screen and (max-width: 425px){
    h2.home__text.pz__10{
    font-size: 32px;
}
h3.sweet.pz__10 {
    font-size: 40px;
}
h1.home__text {
    font-size: 14px;
}
h4.home__text {
    font-size: 45px;
}
}