
.contact{
    background-image: url(./img/contact.jpg);
background-size: cover;
background-repeat: no-repeat;
background-position: center center;
}
.contact__box{
   
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   
    display: inline-block;
    margin: 30px;
}


h1.hire__text {
    font-size: 60px;
}
p.hire__text{
    font-size: 18px;
    line-height: 30px;
    color: rgba(29,29,36,.75);
    font-weight: 400;
}
input.contact{
    
    width: 55%;
    padding: 0 20px;
    border: 2px solid rgba(0,0,0,.1);
    border-radius: 5px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    height: 55px;
    line-height: 46px;
    margin: 20px 0px;
    outline: none;
    color: #fff;
    font-size: 15px;
    letter-spacing: .1px;
    background: none;
    border: 4px solid #fff;
    border-radius: 5px;
    
}

 textarea#message{
    height: 120px;
    background: none;
    padding: 0 20px;
    width: 55%;
    line-height: 46px;
    color: #fff;
    font-size: 15px;
    letter-spacing: .1px;
    border: 4px solid #fff;
    border-radius: 5px;
    
}

button.btn.contact.pointer {
    margin: 20px 0px;
    display: block;
    
}
button.btn.contact.pointer:hover {
    color: #F9004D;
}
